/* =================================
Hero styles
==================================== */
.hero {
    background: url("../img/photos/header.jpg") no-repeat center center;
    background-size: cover;
    height: 60vh;
    margin-bottom: 1rem;
    padding: 1.5rem 3rem;
}
.hero .info {
    float: right;
    margin-top: 47vh;
}
.hero .info .attribution {
    color: $color-default;
    font-family: $font-family--secondary;
    font-size: 0.95rem;
    float: left;
    text-align: right;
}
.hero .info .attribution b {
    color: $color-primary;
    display: block;
    font-weight: $font-weight--medium;
}
.hero .info .avatar {
    border-radius: 50%;
    float: right;
    height: 50px;
    margin-left: 1rem;
    width: 50px;
}
