/* =================================
Photo grid Styles
==================================== */
.image-grid {
    padding: 0 3rem;
}
.image-grid h2 {
    color: $color-primary;
    font-family: $font-family--primary;
    font-size: 1.5rem;
    font-style: italic;
    font-weight: $font-weight--normal;
}
.image-grid .row {
    max-width: 100%;
    margin-bottom: 1rem;
}
.image-grid .columns {
    padding-left: 0;
}

.image-grid .image a {
    border-radius: .5rem .5rem 0 0;
    display: block;
    min-width: 100%;
    overflow: hidden;
}
.image-grid .image .info {
    background-color: $white;
    border-bottom: 2px solid;
    border-bottom-color: $color-border;
    border-radius: 0 0 .5rem .5rem;
    padding: 1rem;
}
.image-grid .image .info .attribution {
    color: $color-primary;
    font-family: $font-family--secondary;
    font-size: 0.95rem;
}
.image-grid .image .info .attribution b {
    display: block;
    font-weight: $font-weight--medium;
}
.image-grid .image .info .avatar {
    border-radius: 50%;
    float: left;
    height: 50px;
    margin-right: 1rem;
    width: 50px;
}
.icon-avatar1 {
  object-fit: none;
  object-position: 0px 0px;
}
.icon-avatar2 {
  object-fit: none;
  object-position: -50px 0px;
}
.icon-avatar3 {
  object-fit: none;
  object-position: 0px -50px;
}
.icon-avatar4 {
  object-fit: none;
  object-position: -50px -50px;
}
.icon-avatar5 {
  object-fit: none;
  object-position: -100px 0px;
}
.icon-avatar6 {
  object-fit: none;
  object-position: -100px -50px;
}
.icon-avatar7 {
  object-fit: none;
  object-position: 0px -100px;
}
